import React, { useState } from "react";
import Message from "./Message";
import SendIcon from '@mui/icons-material/Send';
import { Configuration, OpenAIApi } from "openai";

function ChatWindow() {

    const [message, setMessage] = useState("");
    const [messageArray, setMessageArray] = useState([{
        user: "bot",
        message: "Hello visitor!"
    }, {
        user: "bot",
        message: "I'm a virtual assistant that tells you the weather of a city. Chat with me!"
    }]);

    function buildMessage(event) {
        const text = event.target.value;
        setMessage(text);
    }
    //RASA
    function sendMessage(event) {

        if (message !== "") {
            const payloadmessage = {
                user: "client",
                message: message
            }

            setMessageArray(previousMessages => {
                return [...previousMessages, payloadmessage]
            })
            setMessage("");

            fetch('https://a75a-148-69-1-5.eu.ngrok.io/webhooks/rest/webhook', {
                method: 'POST',
                mode: 'cors',
                headers: {
                    'Content-Type': 'application/json',
                },
                body: JSON.stringify({
                    sender: "client",
                    message: message
                })
            })
                .then((response) => response.json())
                .then((data) => {

                    data.map(response => {

                        if (response.text) {
                            const botResponse = {
                                user: "bot",
                                message: response.text
                            }

                            return setMessageArray(previousMessages => {
                                return [...previousMessages, botResponse]
                            })
                        } else return null;

                    })

                })
                .catch((error) => {
                    console.log(error)
                    const botResponse = {
                        user: "bot",
                        message: "The assistant may be offline. Please try again later"
                    }

                    return setMessageArray(previousMessages => {
                        return [...previousMessages, botResponse]
                    })
                });

        }

        event.preventDefault();
    }

    return (
        <div className="chatComponent">
            <div className="chatBody">
                {messageArray.slice(0).reverse().map((element, index) => {

                    return <Message key={index} index={index} user={element.user} message={element.message} />

                })}
            </div>
            <div className="chatInput">
                <form>
                    <input type="text" onChange={buildMessage} value={message} id="chatText" name="chatText" placeholder="Type your message..." autoComplete="off"></input>
                    <button className="send" onClick={sendMessage} type="submit"><SendIcon /></button>
                </form>
            </div>
        </div>
    );
}

export default ChatWindow;